.sidebar {
    position: sticky;
    top: 80px;
    flex: 0.2;
    
    border-radius: 10px;
    height: fit-content;
    text-align: center;
    margin-right: 20px;
  }
  
  .sidebar__avatar {
    margin-bottom: 10px;
  }
  
  .sidebar__top {
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid lightgray;
    border-bottom: 0;
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
    background-color: white;
    padding-bottom: 10px;
  }
  
  .sidebar__top > img {
    margin-bottom: -20px;
    width: 100%;
    height: 60px;
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
    object-fit: cover;
  }

  .sidebar__top >h4 {
    color: gray;
    font-size: 12px;
  }

  .sidebar__top > h2 {
    font-size: 18px;
  }

  .sidebar__stats {
    width: 100%;
    margin-top: 10px;
    border-top: 1px solid lightgrey;
    padding: 10px;
    margin-bottom: 10px;
    box-sizing: border-box;
  }
  .sidebar__stats-item {
    margin-top: 10px;
    display: flex;
    justify-content: space-between;
  }

  .sidebar__stats-item > p {
    width: 100%;
    COLOR: GRAY;
    font-size: 13px;
    font-weight: 600;

  }

  .sidebar__stats-item-value {
    width: 100%;
    font-weight: bold;
    color: #0a6672 !important;
  }
  

.sidebar__bottom {
    text-align: left;
    padding: 10px;
    margin-top: 10px;
    border: 1px solid lightgrey;
    background-color: white;
    border-radius: 10px;
}


.sidebar__recent-item {
    display: flex;
    font-size: 13px;
    font-weight: bolder;
    color: grey;
    cursor: pointer;
    margin-bottom: 20px;
}

.sidebar__recent-item:hover {
    background-color: whitesmoke;
    color: black;
    cursor: pointer;
    border-radius: 10px;
}

.sidebar_hashtag {
    margin-left: 10px;
    margin-right: 10px;
}

.sidebar__bottom > p {
    font-size: 13px;
    padding-bottom: 20px;
}

